import React from "react";
import { observer, inject } from "mobx-react";
import { Card } from "semantic-ui-react";
import { useDrag, DragSourceMonitor } from "react-dnd";
import { ItemTypes } from "../../App";
import PlayMaker from "../../logic/store";

interface IProps {
  cardId: number;
  backsideUp: boolean;
  selectable: boolean;
  store?: PlayMaker;
}

const PlayingCard = inject("store")(
  observer((props: IProps) => {
    const { backsideUp, cardId, store } = props;

    const [{ isDragging }, drag] = useDrag({
      item: { name: cardId, type: ItemTypes.CARD },
      end: (item: { name: string } | undefined, monitor: DragSourceMonitor) => {
        const dropResult = monitor.getDropResult();
        if (item && dropResult) {
          props.store!.emitCardMove(dropResult.name, Number(item.name));
        }
      },
      collect: monitor => ({
        isDragging: !!monitor.isDragging()
      })
    });

    const card = store!.getPlayingCard(cardId);

    const displayValue = card.value === "Joker" ? "" : card.value;

    return (
      <div
        ref={drag}
        style={{
          width: 50,
          opacity: isDragging ? 0.5 : 1,
          cursor: "move",
          margin: "0 auto"
        }}
      >
        <Card
          onClick={() => {
            if (props.selectable) {
              props.store?.selectCard(props.cardId);
            }
          }}
          style={{ width: 50, height: 70 }}
        >
          {backsideUp ? (
            <img
              src={"/mulle-card-game/img/bagside.png"}
              alt=""
              style={{ width: "100%" }}
            />
          ) : (
            <div
              style={{
                position: "relative",
                height: 70,
                width: 50,
                padding: 8,
                fontWeight: "bold",
                border: "1px solid #DDD",
                borderRadius: 5
              }}
            >
              {card.value === "Joker" ? (
                <>
                  <img
                    src={`/mulle-card-game/img/${card.color}`}
                    alt=""
                    style={{ width: 40, height: 40 }}
                  />
                </>
              ) : (
                <>
                  <img
                    src={`/mulle-card-game/img/${card.color}`}
                    alt=""
                    style={{
                      width: 30,
                      height: 30,
                      position: "absolute",
                      top: 0,
                      right: 10
                    }}
                  />
                  {card.number <= 10 && (
                    <div
                      style={{
                        width: 25,
                        textAlign: "center",
                        right: 15,
                        top: 35,
                        fontSize: 30,
                        color:
                          card.color === "ruder.png" ||
                          card.color === "hjerter.png"
                            ? "red"
                            : "black",
                        position: "absolute"
                      }}
                    >
                      {card.value}
                    </div>
                  )}

                  {card.number > 10 && (
                    <>
                      <img
                        src={`/mulle-card-game/img/${card.number}.png`}
                        alt=""
                        style={{
                          width: 45,
                          height: 45,
                          position: "absolute",
                          top: 25,
                          right: 5
                        }}
                      />
                    </>
                  )}
                </>
              )}
            </div>
          )}
        </Card>
      </div>
    );
  })
);

export default observer(PlayingCard);
